import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { Row, Col, Container } from 'react-bootstrap';
import emailjs from "emailjs-com";





function NutritionistScreen() {

    function sendEmail(e) {
        e.preventDefault();

    emailjs.sendForm('gmail', 'template_xda61uu', e.target, 'user_o6EfGPIavKB6feRQTLbwp')
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });
        e.target.reset()
        alert('Message send!')
    }

    return(
       <Container className ='mb-3'>
       <div>
       <div className = "search-header">
        <h1 className = "look-for"> Nutrition Advice </h1>
       </div>
        <p className='text-center'>For more enquires, contact our nutritionist</p>
       <div className="container">
       <form onSubmit={sendEmail}>
               <div className="row pt-5 mx-auto">
                   <div className="col-8 form-group mx-auto">
                       <input type="text" className="form-control" placeholder="Name" name="name"/>
                   </div>
                   <div className="col-8 form-group pt-2 mx-auto">
                       <input type="email" className="form-control" placeholder="Email Address" name="email"/>
                   </div>
                   <div className="col-8 form-group pt-2 mx-auto">
                       <input type="text" className="form-control" placeholder="Subject" name="subject"/>
                   </div>
                   <div className="col-8 form-group pt-2 mx-auto">
                       <textarea className="form-control" id="" cols="30" rows="8" placeholder="Your message" name="message"></textarea>
                   </div>
                   <div className="col-8 pt-3 mx-auto">
                       <input type="submit" className="btn" value="Send Message"></input>
                   </div>
               </div>
           </form>
       </div>
   </div>
       </Container>
    )

}

export default NutritionistScreen;