import React, { useEffect, useState }  from 'react';
import { Container, Row, Col, Form, Button} from 'react-bootstrap';

import Recipe from '../../components/recipe/recipe';
import {Link} from 'react-router-dom';



import './recipies.styles.scss';




const Recipies = () => {

    const APP_ID = "e8ee2b6a";
    const APP_KEY = "9e2875f6b5f51c962b2fb7898af7cfe9";
    
    const [recipes, setRecipes] = useState([]);
    const [search, setSearch] = useState('');
    const [query, setQuery] = useState('avocado');

  
    useEffect (() => { getRecipes() }, [query])  // run every time when the element in [] changes, if empty [], only run once it is mounted
  
    const getRecipes = async () => {
      const response = await fetch(
        `https://api.edamam.com/search?q=${query}&avocado&app_id=${APP_ID}&app_key=${APP_KEY}`
      )
      const data = await response.json();  // use `await` or `.then` every time use promit
      setRecipes(data.hits);
      console.log(data.hits)
    }
  

    const updateSearch = e => {
        setSearch(e.target.value);
      }
    
      const getSearch = e => {
        e.preventDefault();
        setQuery(search);
      }
    
      return(

        
        
        <div className="recipe-page"        >
          <div className = "search-header">
            <h1 className = "look-for"> Look for recipe </h1>
          </div>
          <Container>
          <form className="search-form " onSubmit={getSearch}>
            <input className="search-bar p-2" type="text" value={search} onChange={updateSearch} />
            <button className="search-button button" type="submit">
              Search
            </button>
          </form>
          </Container>

          <div className="Recipes">
            {recipes.map(recipe => (
              <Recipe
                dishType = {recipe.recipe.dishType}
                totalWeight={recipe.recipe.totalWeight}
                key={recipe.recipe.label}
                title={recipe.recipe.label}
                calories={recipe.recipe.calories}
                image={recipe.recipe.image} 
                ingredients={recipe.recipe.ingredients}
                url = {recipe.recipe.url}
                healthLabels = {recipe.recipe.healthLabels}
              />
            ))}
          </div>
        </div>
     
      );

}


export default Recipies;

