import React from 'react';
import {Container, Row, Col } from 'react-bootstrap';

function Footer() {
    return (
               
        <div className = "footer-wrapper">
      
        <div className='footer'>
            <Row>
                <Col lg={4} className='footer-text'>
                    <h1>CONTACT</h1>
                    <p>ambrosia@gmail.com</p>
                    <p>xxx xxx xxx</p>
                </Col>
                <Col lg={4} className='footer-text office' >
                    <h1>Office hours</h1>
                    <p>Monday-Friday<br/>8-16</p>
                </Col>
                <Col lg={4} className='footer-text  develop ' >
                    <ion-icon name="logo-facebook"></ion-icon>
                    <ion-icon name="logo-instagram"></ion-icon>
                    <ion-icon name="logo-linkedin"></ion-icon>
                </Col>
    
            </Row> 
            <Row>
                <Col lg={4} className='footer-text contact  '>
                </Col>
                <Col lg={4} className='footer-text contact '>
                </Col>
    
                <Col lg={4} className='footer-text develop  '>      
                </Col>
            </Row>
            <Row>
            <Col lg={9} className='footer-text contact '>
                <p className="terms-of-use"> 2021 Ambrosia | Terms of Use | Privacy Policy |  Copyright Policy |  Accessibility |  *See Sources</p>
                <p className="website">Website developed by <span class="name">Justyna Bucko</span></p>
            </Col>
            </Row>
        </div>
    </div>
    )
}

export default Footer;