
import React from 'react';
import {Route,Link, Switch} from 'react-router-dom';

import './recipe.styles.scss';

const Recipe = ({ title, calories, image, ingredients, totalWeight, url,healthLabels}) => {

  function openTab() {
    window.open(url);
  }

  function open() {
    window.open(image);
  }


    return(
      <div className="recipe">
        <h1>{title}</h1>
        <p className="weight">total weight: {Math.round(totalWeight)} g  </p>
        <p class="i-title">ingridients</p>
        <ol className = 'list'>
          {ingredients.map(ingredient => (
            <li className = "recipe-text">
              {ingredient.text}      
            </li>
          ))}
        </ol>


        <div className = 'health-labels'>
       
      <div className ='image-wrapper'>
        <img src={image} alt=""  onClick={openTab}/>
      </div>
      </div>
        </div>
    )
  }
  
  export default Recipe;